<template>
  <el-divider content-position="left" style="background-color: rgba(0,0,0,0)"><h1
      style="font-size: 20px;background-color: rgba(0,0,0,0)">推荐歌单</h1></el-divider>
  <div class="recommend-song-list-container" v-loading="!recommendSongList">
    <div class="recommend-song-list-item"
         v-for="(item, index) in recommendSongList" :key="index"
         @click="routerToSongListPage(item.id)"
    >
      <div class="recommend-song-list-item-cover">
        <!-- 歌单封面 -->
        <el-image :src="item.picUrl" fit="fill"/>
        <!-- 播放量 -->
        <div class="play-count">
          <img class="play-count-img" src="@/assets/image/play_2.svg" alt="">
          {{
            item.playCount >= 10000 ? (item.playCount / 10000).toFixed(0) + "万" : item.playCount
          }}
        </div>
        <!-- 播放图标 -->
        <img class="play-img" src="@/assets/image/play_1.svg" alt=""/>
      </div>
      <!-- 描述 -->
      <p class="recommend-song-list-item-intro">{{ item.name }}</p>
    </div>
  </div>
</template>

<script>
import {ref, onMounted} from 'vue'
import axios from "axios";
import {useRouter} from 'vue-router'

export default {
  name: "RecommendSongList",
  setup() {
    const router = useRouter()
    let recommendSongList = ref([])
    onMounted(() => {
      axios.get("/personalized", {
        params: {
          limit: 10,
        },
      }).then((res) => {
        console.log(res.data)
        recommendSongList.value = res.data.result;
      });
    })

    function routerToSongListPage(songListId) {
      router.push('/home/song-list/' + songListId)
    }

    return {
      recommendSongList,
      routerToSongListPage
    }
  },

}
</script>

<style scoped>
>>> .el-divider__text {
  background-color: rgba(0, 0, 0, 0);
}

.recommend-song-list-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.recommend-song-list-item {
  width: 18%;
  margin: 1%;
}

.recommend-song-list-item-cover {
  position: relative;
  border-radius: 10px;
  box-shadow: grey 0px 0px 1px 1px;
  overflow: hidden;
}

.recommend-song-list-item .el-image {
  display: block;
  width: 100%;
}


.play-count {
  height: 18px;
  font-size: 16px;
  line-height: 18px;
  color: white;
  position: absolute;
  top: 2%;
  right: 2%;
  text-shadow: 0px 0px 2px #000000;
}

.play-count-img {
  width: 16px;
}

.play-img {
  position: absolute;
  width: 30px;
  height: 30px;
  z-index: 1;
  right: 20px;
  bottom: 20px;
  border-radius: 50%;
  background: #f2f3f4;
  opacity: 0;
  transition: opacity 0.5s;
}

.recommend-song-list-item:hover .play-img {
  opacity: 100%;
}

.recommend-song-list-item-intro {
  font-size: 16px;
}
</style>