<template>
  <!--轮播图-->
  <el-carousel :interval="4000" type="card" height="200px">
    <el-carousel-item v-for="(item, index) in bannerInfo" :key="index">
      <el-image
          :src="item.imageUrl"
          fit="fill"
          style="height: 90%; border-radius: 10px"
      ></el-image>
      <el-tag :type="item.typeTitle === '独家' ? 'danger' : ''"
              effect="dark"
              style="position: absolute; bottom: 10%; right: 0; border-radius: 5px"
      >
        {{ item.typeTitle }}
      </el-tag>
    </el-carousel-item>
  </el-carousel>
</template>

<script>
import {ref,onMounted} from 'vue'
import axios from "axios";
export default {
  name: "Banner",
  setup() {
    let bannerInfo = ref([])
    onMounted(()=>{
      axios.get("banner").then((res) => {
        bannerInfo.value = res.data.banners
      })
    })
    // ;(async () => {
    //   const res = await axios.get("banner");
    //   bannerInfo.value = res.data;
    //   console.log(res);
    // })()
    return{
      bannerInfo
    }
  },

}
</script>

<style scoped>

.el-carousel__item:nth-child(2n) {
  background-color: rgba(0, 0, 0, 0);
}

.el-carousel__item:nth-child(2n + 1) {
  background-color: rgba(0, 0, 0, 0);
}
</style>